<template>
   <div class="setup calc-section">
      <span class="title">По диапазону дат</span>
      <input
         type="date"
         v-model="dateFrom"
         @change="dateChangeHandler"
         :max="dateTo"
      />
      <input
         type="date"
         v-model="dateTo"
         @change="dateChangeHandler"
         :min="dateFrom"
      />

      <span class="title">По отрезку времени</span>
      <input type="number" @input="timeInputHandler" v-model="time" />
      <select v-model="timeFormat" @change="timeFormatChangeHandler">
         <option>минут</option>
         <option>часов</option>
         <option>суток</option>
         <option>лет</option>
      </select>

      <span class="title">Начальная активность</span>
      <input type="number" v-model="a0" />
      <select v-model="aFormat" @change="aFormatChangeHandler">
         <option>Бк</option>
         <option>кБк</option>
         <option>МБк</option>
         <option>ГБк</option>
      </select>

      <button
         class="calculate-btn"
         :disabled="!percentOk || !setupOk"
         @click="calculateClickHandler"
      >
         Рассчитать
      </button>
   </div>
</template>

<script>
export default {
   name: 'Setup',
   props: {
      percentOk: Boolean
   },
   data() {
      return {
         dateFrom: null,
         dateTo: null,
         time: null,
         timeFormat: 'суток',
         aFormat: 'Бк',
         a0: null
      }
   },
   computed: {
      timeMin() {
         if (!this.time) return 0
         let mult
         switch (this.timeFormat) {
            case 'минут':
               mult = 1
               break
            case 'часов':
               mult = 60
               break
            case 'суток':
               mult = 1440
               break
            case 'лет':
               mult = 525948.768 // 525600
               break
         }
         return this.time * mult
      },
      aBk() {
         if (!this.a0) return 0
         let mult
         switch (this.aFormat) {
            case 'Бк':
               return this.a0
            case 'кБк':
               mult = 1000
               break
            case 'МБк':
               mult = 1000000
               break
            case 'ГБк':
               mult = 1000000000
               break
         }
         return this.a0 * mult
      },
      setupOk() {
         return this.timeMin > 0 && this.a0 > 0
      }
   },
   methods: {
      dateChangeHandler() {
         if (this.dateFrom && this.dateTo) {
            // get time in days
            this.time =
               (new Date(this.dateTo).getTime() -
                  new Date(this.dateFrom).getTime()) /
               (1000 * 3600 * 24)
            this.timeFormat = 'суток'
         }
      },
      timeInputHandler(e) {
         this.resetDates()
         this.time = e.target.value
      },
      timeFormatChangeHandler(e) {
         this.resetDates()
         this.timeFormat = e.target.value
      },
      resetDates() {
         this.dateFrom = null
         this.dateTo = null
      },
      aFormatChangeHandler(e) {
         this.aFormat = e.target.value
      },
      calculateClickHandler() {
         this.$emit('calculate', this.aBk, this.timeMin)
      }
   }
}
</script>

<style scoped lang="scss">
.setup {
   padding: 20px;
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   grid-gap: 10px;
}
.calculate-btn {
   grid-column: 3;
   padding: 10px;
   color: white;
   font-size: 18px;
   border: none;
   border-radius: 5px;
   background: linear-gradient(
      145deg,
      rgb(29, 54, 120) 0%,
      rgb(80, 155, 209) 100%
   );
   opacity: 0.5;
   &:enabled {
      opacity: 1;
      &:hover {
         background: linear-gradient(
            145deg,
            rgb(39, 44, 130) 0%,
            rgb(90, 165, 219) 100%
         );
      }
   }
}
</style>
