<template>
   <div class="result calc-section">
      <div
         class="active-item"
         v-for="(item, i) in active"
         :key="'active_item_' + item.ID"
      >
         <div class="stats">
            <span class="active-stat"
               >Наименование:
               <span class="name">{{ item.item.Name_RN }}</span>
            </span>
            <span class="active-stat">{{
               'Обозначение: ' + item.item.Name_RN_Lat
            }}</span>
            <span class="active-stat">{{
               'Период полураспада: ' +
               item.item.Period_p_r +
               ' ' +
               item.item.Edinica_izmer_p_r
            }}</span>
         </div>
         <div class="active-input-container" v-if="this.active.length > 1">
            <input
               :class="['active-input', { error: !percentOk }]"
               type="number"
               :value="Number(item.percent.toFixed(2))"
               @input="percentInputHandler($event, i)"
            />
            %
         </div>
         <div
            class="active-input-container result-container"
            v-if="item.result != undefined"
         >
            <input
               readonly
               class="active-input result"
               type="text"
               :value="item.result"
            />
            Бк
         </div>
         <div class="active-result"></div>
         <button class="remove-btn" @click="removeClickHandler(i)">X</button>
      </div>
   </div>
</template>

<script>
export default {
   name: 'Result',
   props: {
      active: Array,
      percentOk: Boolean
   },
   data() {
      return {}
   },
   methods: {
      removeClickHandler(i) {
         this.$emit('remove-item', i)
      },
      percentInputHandler(e, i) {
         const value = e.target.value
         this.$emit('percent-input', i, value)
      }
   }
}
</script>

