<template>
   <div class="calc">
      <List :list="filteredList" @add-item="addItem" />
      <div class="right">
         <Setup :percentOk="percentOk" @calculate="calculate" />
         <Result
            :active="active"
            :percentOk="percentOk"
            @remove-item="removeItem"
            @percent-input="percentInput"
         />
      </div>
   </div>
</template>

<script>
import List from './list.vue'
import Setup from './setup.vue'
import Result from './result.vue'

export default {
   name: 'Calc',
   components: { List, Setup, Result },
   props: {
      url: String
   },
   data() {
      return {
         list: [],
         active: []
      }
   },
   computed: {
      filteredList() {
         const activeIds = this.active.map(item => item.item.ID)
         return this.list.filter(item => !activeIds.includes(item.ID))
      },
      totalPercent() {
         let totalPercent = this.active.reduce((p, c) => p + c.percent, 0)
         totalPercent = Number(totalPercent.toFixed(12)) // fix 100.0000000000001
         return totalPercent
      },
      percentOk() {
         return this.active.length != 0 && this.totalPercent == 100
      }
   },
   methods: {
      addItem(item) {
         // calculate percentages
         let percent
         if (this.totalPercent == 100) {
            // existing items sum up to 100%
            if (
               this.active.filter(i => i.percent == 100 / this.active.length)
                  .length == this.active.length
            ) {
               // even percentage spread
               percent = 100 / (this.active.length + 1)
               this.active.forEach(i => (i.percent = percent))
            } else {
               // uneven percentage spread
               percent = 0
            }
         } else {
            // existing items don't sum up to 100%
            percent = 100 - this.totalPercent
         }
         this.active.push({ item, percent })

         this.resetCalculations()
      },
      removeItem(i) {
         const percent = this.active.splice(i, 1)[0].percent

         if (this.active.length) {
            if (this.active.length == 1) {
               // only one active item left, set it to 100%
               this.active[0].percent = 100
            } else {
               // more than one item left
               // add percentage of removed to the first active item
               this.active[0].percent += percent
            }
         }

         this.resetCalculations()
      },
      percentInput(i, value) {
         value = parseFloat(value) || 0
         if (!value) this.active[i].percent = 1 // force update of the input
         this.active[i].percent = value

         this.resetCalculations()
      },
      calculate(a0, timeMin) {
         // calculate resulting activity for each active item
         this.active.forEach(item => {
            // A = A0 *2 ^ (-t / T½)
            item.result = a0 * 2 ** (-timeMin / item.item.Period_p_r)
            // * percent
            item.result *= item.percent * 0.01
         })
      },
      resetCalculations() {
         this.active.forEach(item => delete item.result)
      }
   },
   created() {
      // fetch json
      fetch(this.url)
         .then(res => res.json())
         .then(data => {
            this.list = data.filter(item => {
               // filter out all the items with no name, code, or period specified
               return (
                  item.Name_RN &&
                  item.Kod_RN &&
                  item.Period_p_r &&
                  item.Name_RN_Lat
               )
            })
         })
   }
}
</script>

<style scoped lang="scss">
.calc {
   display: flex;
   .right {
      display: flex;
      flex-direction: column;
      flex-grow: 1;
   }
   .calc-section {
      background: #ecf5fa;
      margin: 10px;
      border-radius: 3px;
   }
}
</style>
